.App {
    width: 100vw;
    height: 100vh;
    background: rgb(240, 242, 245);
}

.container {
    background-image: url(./img/bg.png);
    background-repeat: no-repeat;
    min-height: 100%;
    position: relative;
}

.mobile-wrapper {
    background-size: 600% 120%;
}

.pc-inner {
    width: 1000px;
    margin: 0 auto;
}

.mobile-inner {
    padding: 20px 16px 190px;
}
.title {
    margin-bottom: 38px;
    font-weight: 600;
    font-size: 36px;
    color: #333;
    text-align: center;
    padding-top: 42px;
}

.row-box {
    border-radius: 8px;
    background: #fff;
    display: flex;
    margin-bottom: 10px;
}
.first-row-box {
    height: 206px;
    padding-top: 16px;
    padding-bottom: 24px;
}

.mobile-box {
    height: auto;
    flex-direction: column;
}

.second-row-box {
    padding-top: 24px;
    padding-bottom: 24px;
}

.search-wrapper {
    width: 571px;
    border-right: 1px solid #ddd;
    padding-left: 20px;
    padding-right: 75px;
}

.mobile-search-wrapper {
    width: auto;
    padding-right: 0;
    padding-left: 0;
    padding-bottom: 10px;
    margin: 0 20px 10px;
    border-right: none;
    border-bottom: 1px solid #ddd;
}
.search-title {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}
.search-title-text {
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    color: #25408f;
    margin-left: 10px;
    margin-right: 16px;
    font-family: PingFangSC-Semibold, 阿里巴巴普惠体, 微软雅黑;
}

.search-title-tip {
    font-size: 12px;
    margin-left: 25px;
    margin-top: 6px;
    color: #666;
    margin-bottom: 20px;
    margin-right: -45px;
}
.mobile-search-title-tip {
    font-size: 12px;
    margin-left: 0;
    margin-top: 6px;
    color: #666;
    margin-bottom: 20px;
    margin-right: 0;
}

.search-input-wrapper {
    display: flex;
    align-items: center;
    margin-left: 24px;
}
.mobile-search-input-wrapper {
    display: flex;
    align-items: flex-start;
    margin-left: 0;
    flex-direction: column;
}

.children {
    width: 100%;
    display: flex;
    margin-bottom: 10px;
}
.result-wrapper {
    text-align: center;
    margin: 0 46px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1;
}

.result-title {
    font-size: 18px;
    color: #25408f;
    margin-top: 14px;
    margin-bottom: 6px;
    text-align: center;
}

.result-content {
    font-size: 14px;
    text-indent: 26px;
    text-align: left;
    display: inline-block;
    word-break: break-all;
    line-height: 24px;
}

.scan-result {
    position: absolute;
    top: 16px;
    right: 16px;
    bottom: 0;
    left: 16px;
    background: #fff;
    padding-top: 30px;
    z-index: 10;
}

.more-btn {
    display: flex;
    flex-direction: column;
    padding-left: 16px;
    padding-right: 16px;
    margin-top: 30px;
}

.form-item {
    display: flex;
    margin-left: 23px;
}
.mobile-from-item {
    display: flex;
    margin-left: 0;
}
.label {
    white-space: nowrap;
}
.form-content {
    flex: 1;
}
.form-content-title {
    margin-bottom: 7px;
}
.second-tip {
    color: #666;
    font-size: 12px;
    margin-right: -60px;
    margin-bottom: 10px;
}
.mobile-second-tip {
    color: #666;
    font-size: 12px;
    margin-right: 0;
    margin-bottom: 10px;
    margin-left: -55px;
}
.input-item {
    display: flex;
    line-height: 32px;
    margin-bottom: 10px;
}
.mobile-input-item {
    display: flex;
    line-height: 32px;
    margin-bottom: 10px;
    margin-left: -55px;
}
.input-label {
    margin-right: 8px;
    white-space: nowrap;
    min-width: 67px;
    text-align: right;
}
.input-content {
    width: 210px;
}
.ant-btn {
    min-width: 72px;
}

.ant-form-item-required::before {
    display: inline-block;
    margin-right: 4px;
    color: #ff4d4f;
    font-size: 14px;
    font-family: SimSun, sans-serif;
    line-height: 1;
    content: "*";
}

.unit {
    color: #888;
    width: 12px;
    min-width: 12px;
    font-size: 12px;
    margin-left: 10px;
    white-space: nowrap;
}

.file-input-wrapper {
    display: flex;
}

.mobile-file-input-wrapper {
    display: flex;
    margin-left: -55px;
}

.file-input {
    flex: 1;
    display: flex;
    align-items: center;
    margin-right: 10px;
    background: white;
    border-radius: 4px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.upload-btn {
    height: 40px;
    padding: 0 15px;
    font-size: 16px;
    border-radius: 4px;
    border: 1px dashed #bbb;
    background-color: white;
}
